import React, { createContext, useContext, useState, useEffect } from 'react';
import axios from 'axios';
import Papa from 'papaparse';

const DataContext = createContext();

export const useData = () => {
  const context = useContext(DataContext);
  if (!context) {
    throw new Error('useData doit être utilisé dans un DataProvider');
  }
  return context;
};

export const DataProvider = ({ children }) => {
  const [data, setData] = useState({
    publications: [],
    team: [],
    research: [],
    tools: [],
    news: [],
  });
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchData = async () => {
      try {
        console.log('Starting data fetch...');
        
        const urls = {
          publications: 'https://docs.google.com/spreadsheets/d/e/2PACX-1vRvv_lUiP6sK7A7QTxMI0eA3kllA9oj-D2Ee7BnCivHov_iKQ6p1CH96be-iMGi1_bDtMASdEC2Ibmi/pub?gid=0&single=true&output=csv',
          team: 'https://docs.google.com/spreadsheets/d/e/2PACX-1vRvv_lUiP6sK7A7QTxMI0eA3kllA9oj-D2Ee7BnCivHov_iKQ6p1CH96be-iMGi1_bDtMASdEC2Ibmi/pub?gid=1060799501&single=true&output=csv',
          research: 'https://docs.google.com/spreadsheets/d/e/2PACX-1vRvv_lUiP6sK7A7QTxMI0eA3kllA9oj-D2Ee7BnCivHov_iKQ6p1CH96be-iMGi1_bDtMASdEC2Ibmi/pub?gid=1203628962&single=true&output=csv',
          tools: 'https://docs.google.com/spreadsheets/d/e/2PACX-1vRvv_lUiP6sK7A7QTxMI0eA3kllA9oj-D2Ee7BnCivHov_iKQ6p1CH96be-iMGi1_bDtMASdEC2Ibmi/pub?gid=1187415274&single=true&output=csv',
          news: 'https://docs.google.com/spreadsheets/d/e/2PACX-1vRvv_lUiP6sK7A7QTxMI0eA3kllA9oj-D2Ee7BnCivHov_iKQ6p1CH96be-iMGi1_bDtMASdEC2Ibmi/pub?gid=1180969958&single=true&output=csv',
          contact: 'https://docs.google.com/spreadsheets/d/e/2PACX-1vRvv_lUiP6sK7A7QTxMI0eA3kllA9oj-D2Ee7BnCivHov_iKQ6p1CH96be-iMGi1_bDtMASdEC2Ibmi/pub?gid=634275759&single=true&output=csv' 
        };

        const [pubResponse, teamResponse, researchResponse, toolsResponse, newsResponse, contactResponse] = await Promise.all([
          axios.get(urls.publications),
          axios.get(urls.team),
          axios.get(urls.research),
          axios.get(urls.tools),
          axios.get(urls.news),
          axios.get(urls.contact)
        ]);

        console.log('All data fetched successfully');

        const parseData = (csvData) => {
          return new Promise((resolve) => {
            Papa.parse(csvData, {
              header: true,
              skipEmptyLines: true,
              complete: (results) => resolve(results.data)
            });
          });
        };

        const [publications, team, research, tools, news, contact] = await Promise.all([
          parseData(pubResponse.data),
          parseData(teamResponse.data),
          parseData(researchResponse.data),
          parseData(toolsResponse.data),
          parseData(newsResponse.data),
          parseData(contactResponse.data)
          
        ]);

        console.log('Publications count:', publications.length);
        console.log('Team members count:', team.length);
        console.log('Research projects count:', research.length);
        console.log('Tools count:', tools.length);
        console.log('News count:', news.length);
        console.log('Contact count:', contact.length);

        setData({
          publications,
          team,
          research,
          tools,
          news,
          contact
        });
        setLoading(false);

      } catch (err) {
        console.error('Error details:', {
          message: err.message,
          response: err.response?.data,
          status: err.response?.status
        });
        setError(err);
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  return (
    <DataContext.Provider value={{ data, loading, error }}>
      {children}
    </DataContext.Provider>
  );
};

export default DataProvider;